import React, {Component} from 'react';
import {connect} from 'react-redux';


class Booking extends Component {
    render() {
        return (
            <div>
                <h1>Haiii</h1>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {...state};
}
export default connect(mapStateToProps)(Booking);
